const userLevel = 0;

class Admin extends Component {


  constructor(props, context) {
    super(props, context);

    console.log(props);

    this.handleShowAdd = this.handleShowAdd.bind(this);
    this.handleCloseAdd = this.handleCloseAdd.bind(this);
    this.handleShowMinus = this.handleShowMinus.bind(this);
    this.handleCloseMinus = this.handleCloseMinus.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.loadData     = this.loadData.bind(this);
    this.redirect     = this.redirect.bind(this);


    this.state = {
      redirect: false,
      show: false,
      show2: false,
      modify_num: '',
      hours: 0,
      row: [],
      users: [],
      email: '',
      finalHours: null,
      admin: null,
      loaded: false,
    };
  }

  handleCloseAdd() {

    this.setState({ show: false });

  }

  handleShowAdd(e) {
    console.log(e);
    this.setState({ show: true });

  }

  handleCloseMinus() {
    this.setState({ show2: false });
  }
  handleShowMinus() {
    this.setState({ show2: true });
  }

  handleSubmit(ref) {

    let axiosBody = {
      stu_no: ref.state.modify_num,
      hourType: (ref.state.finalHours ? 1:0),
      value: ref.state.hours
    }
    console.log(axiosBody);
    axios.post('/api/change-hours', axiosBody).then(res => {
      
      ref.handleCloseAdd();
    }).catch(err => console.log(err));
  }

  handleChange(event) {
    this.setState({hours: event.target.value});
  }

  loadData(accept, reject) {
    axios.get('/api/refresh')
        .then((res) => {
        if(res.data && res.data.userLevel === 0){
          console.log(res.data);
          accept(res);
        }
        else{
          throw 'Bad Response, Contact Support';
        }
      }).catch(() => {
        reject();
      });
  }

  redirect(){
    this.setState({redirect: true});
  }

  componentDidMount() {

    let success = (res)=>{
      // runs on success
      console.log(res.data.data);
      this.setState({
        users: res.data.data,
        admin: res.data.user,
        loaded: true
      });
    }

    let failure = () => {
      this.setState({redirect: true});
    }

    if(!this.props.location.state){
      this.loadData(success, failure);
    }else{
      this.setState({
        users: this.props.location.state.data,
        admin: this.props.location.state.user,
        loaded: true
      });
    }
  }

  render() {
    const rowEvents = {
      onClick: (e, row, rowIndex) => {
        this.setState({ modify_num: row.stu_no });
        this.setState({ email: row.email });
      }
    };
    const add_delete = (cell, row) => {
      //this.setState({modify_num:row.stu_no});
      return (
        <div className="center">
          <div ><i class="fas fa-pencil-alt icon" onClick={() => {this.handleShowAdd(row)}} value={row.stu_no}></i></div>
        </div>
      )
    }

    const toggleFinalHour = () => {
      console.log(this.state.finalHours);
      this.setState({finalHours: !this.state.finalHours});
    }
    
    const columns = [
      {
        dataField: 'fname',
        text: 'First Name'
      }, {
        dataField: 'lname',
        text: 'Last Name'
      }, {
        dataField: 'email',
        text: 'Email'
      }, {
        dataField: 'stu_no',
        text: 'Student Number'
      }, {
        dataField: 'time_reg',
        text: 'Regular Hours Completed'
      }, {
        dataField: 'req_time_reg',
        text: 'Regular Hours Required',
      }, {
        dataField: 'time_final',
        text: 'Final Hours Completed'
      }, {
        dataField: 'req_time_reg',
        text: 'Final Hours Required',
      }, {
        dataField: 'location',
        text: 'Location'
      }, {
        dataField: 'modify',
        text: 'Modify',
        formatter: add_delete,
        editable: false
      }
    ];

    return (
      (this.state.redirect)?(
        <Redirect to={{
          pathname: '/',
        }}/>
      ):(
        (this.state.loaded)?(
          <div className="App">

            <Navbar user={this.state.admin} redirect={this.redirect} /> {/* Our Special imported navbar with logout functionality */}

            <Modal show={this.state.show} onHide={this.handleCloseAdd} centered>
              <Modal.Header closeButton>
                <Modal.Title>Edit Hours of {this.state.email}</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <form>
                  <div class="input input--kohana">
                    <input class="input__field input__field--kohana" type="text" id="hours" name="hours" onChange={this.handleChange}/>
                    <label class="input__label input__label--kohana" for="input-3">
                      <i class="fas fa-fw fa-crown icon-c icon--kohana"></i>
                      <span class="input__label-content input__label-content--kohana"><i class="fas fa-crown"></i>&nbsp;&nbsp;&nbsp;&nbsp;# Of Hours</span>
                    </label>
                  </div>
                  <div class="checkbox-container">
                    Final Hours <input type="checkbox" value="1" onClick={toggleFinalHour}/>
                  </div>
                  
                <button className="btn btn-success" type="submit" onClick={(e) => {e.preventDefault();this.handleSubmit(this)}}>
                  Save
                </button>
                
                </form>

              </Modal.Body>
              <Modal.Footer>
                
              </Modal.Footer>
            </Modal>
            <Row>
              <Col lg={4}>
                <div className="AppTitle" padding="30px">Hours Completed {this.state.hours}</div>
                <div className="center" padding="30px">
                  <CircularProgressbar
                    className="circleContainerStyle"
                    percentage='30'
                    text='30%'
                    styles={{

                      path: { stroke: '#5a058e' },
                      text: { fill: '#5a058e', fontSize: '16px', fontWeight: '800' },
                    }}
                  />

                </div>
              </Col>
              <Col lg={7}>

                <ToolkitProvider
                  keyField="stu_no"
                  data={this.state.users}
                  columns={columns}
                  search
                >
                  {
                    props => (
                      <div>

                        <SearchBar className="" placeholder=" " {...props.searchProps} id="input-23" />


                        <BootstrapTable id="admin-table" pagination={paginationFactory()} rowEvents={rowEvents}
                          {...props.baseProps} hover filter={filterFactory()}
                        />
                      </div>
                    )
                  }
                </ToolkitProvider>
              </Col>
            </Row>
          </div>
        ):(
          <div>
            <h1>Loading</h1> {/* @TODO replace with loading import */}
          </div>
        )
    ));
  }
}

export default Admin;
