import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.css';
import logo from './img/gear.png';
import Nav from 'react-bootstrap/Nav';
import { Navbar, Form, FormControl, Button } from 'react-bootstrap';
import engsoclogo from './img/EngSoc-Logo-Black-2.png';

import {Redirect } from 'react-router';
import axios from 'axios';
import Jumbotron from 'react-bootstrap/Jumbotron';
/*import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';*/
/*import { library } from '@fontawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fontawesome/react-fontawesome';
import { faIgloo } from '@fontawesome/free-solid-svg-icons';*/
import './App.css';
//library.add(faIgloo);

class Manager extends Component {
  constructor(props, context){
    super(props, context);

    this.state = {
      errorCount: 0,
      response: '',
      responseToPost: '',
      cardError:false,
      cardInput: '',
      user: null,
      loaded: false,
      redirect: false
    };

    this.loadData = this.loadData.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  loadData(accept, reject) {

    axios.get('/api/refresh')
        .then((res) => {
          console.log(res);
          if(res.data && res.data.userLevel === 1){
            console.log(res.data);
            accept(res);
          }
          else{
            throw 'Bad Response, Contact Support';
          }
      }).catch((err) => {
        reject();
      });
  }

  componentDidMount() {

    let success = (res) =>{
      // runs on success
      this.setState({
        users: res.data.data,
        loaded: true
      });
    }

    let failure = () => {
      this.setState({redirect: true});
    }

    if(!this.props.location.state){
      this.loadData(success, failure);
    }else{
      this.setState({
        users: this.props.location.state.data,
        loaded: true
      });
    }
  }

  handleSubmit(e){
    e.preventDefault();
    var regex = new RegExp('\\d\\d\\d\\d\\d\\d\\d\\d','');
    var studentNumber = this.state.cardInput.match(regex);
    if(studentNumber){
      this.setState({
        cardInput: '',
        cardError: false
      });
      this.logNumber(studentNumber[0]);
    }
    else{
      this.setState({
        cardInput: '',
        cardError:true,
        responseToPost: 'Unable to read card'
      });
    }
  };

  logNumber(studentNumber){
    this.setState({responseToPost: null});

    let body = {
      user: studentNumber
    }

    axios.post('/api/log-number', body)
      .then((res) => {
        console.log(res);
        this.setState({
          responseToPost: res.data.message,
          cardError: res.data.error
        });
      }).catch((err) => {alert(err); console.log(err)});
  }

  displayMessage = () => {
    if(this.state.responseToPost !== ''){
      if(this.state.cardError){
        return(
          <div className='error-message'>
            <p>
              {this.state.responseToPost}
            </p>
          </div>)
      } 
      else{
        return(
        <div className='success-message'>{/*this.state.responseToPost.error ? 'error-message':'success-message'*/}
          <p>
            <b>{this.state.responseToPost}</b>
          </p>
        </div>)
      }
    }
  }

  render() {
    return (
      (this.state.redirect)?(
        <Redirect to={{
          pathname: '/',
        }}/>
      ):(
        (this.state.loaded)?(
          <div className="App">
          <Navbar bg="light" expand="lg">
              <Navbar.Brand href="#home"><img src={engsoclogo} className="nav-logo" /></Navbar.Brand>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto">
                  <Link to="/" className="Nav-text">Logout</Link>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
            <header className="App-header">
              <img src={logo} className="App-logo" alt="logo" />
              <p>
                Swipe Student Card to Start/Stop Tracking Hours
              </p>
              
            </header>
            <p>{this.state.response}</p>
            <form onSubmit={this.handleSubmit}>
              <p>
                <strong>Click in the input field then swipe card:</strong>
              </p>
              <input
                type="text"
                value={this.state.cardInput}
                onChange={e => this.setState({ cardInput: e.target.value })}
              />
            </form>
            {this.displayMessage()}
          </div>
        ):(
            <div>
              <h1>loading...</h1> {/* @TODO replace with loading import */}
            </div>
        )
      )
    );
  }
}

export default Manager;
